import { useState, useEffect } from "react";
import axios from "axios";

const Profile = ({ userId }) => {
  const [profile, setProfile] = useState({
    name: "",
    email: "",
    profileImage: "",
    resume: "",
  });

  const [imageFile, setImageFile] = useState(null);
  const [resumeFile, setResumeFile] = useState(null);

  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const res = await axios.get(`http://localhost:5000/api/users/${userId}`);
        setProfile(res.data);
      } catch (error) {
        console.log("Error fetching profile:", error);
      }
    };
    fetchProfile();
  }, [userId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("name", profile.name);
    formData.append("email", profile.email);
    if (imageFile) formData.append("profileImage", imageFile);
    if (resumeFile) formData.append("resume", resumeFile);

    try {
      const res = await axios.post("http://localhost:5000/api/users", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
      setProfile(res.data);
      alert("Profile Updated Successfully!");
    } catch (error) {
      console.error("Error updating profile:", error);
    }
  };

  return (
    <div className="max-w-lg mx-auto p-6 bg-white shadow-md rounded-lg">
      <h2 className="text-2xl font-semibold mb-4">Profile</h2>

      <img src={profile.profileImage || "https://via.placeholder.com/150"} alt="Profile" className="w-32 h-32 rounded-full" />
      <input type="file" accept="image/*" onChange={(e) => setImageFile(e.target.files[0])} />

      <input type="text" value={profile.name} onChange={(e) => setProfile({ ...profile, name: e.target.value })} placeholder="Name" />
      <input type="email" value={profile.email} onChange={(e) => setProfile({ ...profile, email: e.target.value })} placeholder="Email" />

      <input type="file" accept=".pdf" onChange={(e) => setResumeFile(e.target.files[0])} />
      {profile.resume && <a href={profile.resume} target="_blank" rel="noopener noreferrer">View Resume</a>}

      <button onClick={handleSubmit}>Save Profile</button>
    </div>
  );
};

export default Profile;
